<template>
    <v-container class="py-10" fluid>
        <v-alert
            v-model="alert"
            :type="alertType"
            border="left"
            close-text="Close Alert"
            elevation="10"
            transition="scale-transition"
            style="
                position: fixed;
                top: 20%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: 9999;
                max-width: 35rem;
            "
        >
            {{ alertMessage }}
        </v-alert>
        <v-dialog
            v-model="dialog"
            fullscreen
            transition="dialog-bottom-transition"
            class="rounded-lg"
        >
            <v-container>
                <v-card class="rounded-lg">
                    <v-toolbar color="green" dark flat mt-4>
                        <v-toolbar-title> Update Slideshow </v-toolbar-title>
                    </v-toolbar>
                    <v-card-text>
                        <v-row>
                            <v-col cols="12" md="6" class="pb-0">
                                <v-text-field
                                    v-model="name"
                                    :label="slideshow.name"
                                    hide-details="auto"
                                    background-color="white"
                                    solo
                                />
                            </v-col>
                            <v-col cols="12" md="6" class="pb-0">
                                <v-text-field
                                    v-model="delay"
                                    :label="slideshow.delay"
                                    hide-details="auto"
                                    suffix="seconds"
                                    solo
                                />
                            </v-col>
                        </v-row>
                        <v-container class="my-4">
                            <draggable
                                v-model="slideshow.slides"
                                group="imageGroup"
                                class="row"
                            >
                                <p v-if="slideshow.slides == ''" class="mt-6">
                                    Drag images here
                                </p>

                                <v-col
                                    v-else
                                    cols="3"
                                    v-for="image in slideshow.slides"
                                    :key="image.id"
                                >
                                    <v-card>
                                        <v-card-text
                                            ><v-img
                                                :src="image.thumbnailUrl"
                                                contain
                                        /></v-card-text>
                                        <v-card-actions
                                            ><v-btn
                                                class="mt-2"
                                                color="warning"
                                                @click="removeSlide(image)"
                                                >Delete</v-btn
                                            ></v-card-actions
                                        >
                                    </v-card>
                                </v-col>
                            </draggable>
                        </v-container>
                        <v-expansion-panels>
                            <v-expansion-panel>
                                <v-expansion-panel-header
                                    ><v-toolbar color="green" dark flat>
                                        <v-toolbar-title
                                            >Media Library</v-toolbar-title
                                        >
                                    </v-toolbar></v-expansion-panel-header
                                >
                                <v-expansion-panel-content>
                                    <v-expansion-panels>
                                        <v-expansion-panel>
                                            <v-expansion-panel-header
                                                >Upload an
                                                image</v-expansion-panel-header
                                            >
                                            <v-expansion-panel-content>
                                                <v-form
                                                    ref="uploadForm"
                                                    v-model="uploadValid"
                                                    lazy-validation
                                                >
                                                    <v-file-input
                                                        class="mx-10"
                                                        chips
                                                        counter
                                                        show-size
                                                        truncate-length="50"
                                                        accept="image/*"
                                                        label="Choose File"
                                                        v-model="originalFile"
                                                        hint="Recommended dimensions: 840 x 480"
                                                        persistent-hint
                                                    />
                                                </v-form>
                                                <v-btn
                                                    right
                                                    @click="uploadMedia"
                                                    class="justify-center"
                                                    style="
                                                        display: block;
                                                        margin: 0 auto 1rem;
                                                    "
                                                >
                                                    Upload
                                                </v-btn>
                                            </v-expansion-panel-content>
                                        </v-expansion-panel>
                                        <v-expansion-panel>
                                            <v-expansion-panel-header
                                                >Choose from media library
                                                images</v-expansion-panel-header
                                            >
                                            <v-expansion-panel-content>
                                                <v-container class="my-4">
                                                    <draggable
                                                        v-model="images"
                                                        :group="{
                                                            name: 'imageGroup',
                                                            pull: 'clone',
                                                            put: 'false',
                                                        }"
                                                        class="row"
                                                    >
                                                        <v-col
                                                            v-for="image in images"
                                                            :key="image.id"
                                                            cols="3"
                                                        >
                                                            <v-card>
                                                                <v-img
                                                                    :src="
                                                                        image.thumbnailUrl
                                                                    "
                                                                    min-width="150"
                                                                />
                                                            </v-card>
                                                        </v-col>
                                                    </draggable>
                                                </v-container>

                                                <v-pagination
                                                    class="mt-6"
                                                    v-model="page"
                                                    circle
                                                    :length="pagesTotal"
                                                    :total-visible="7"
                                                    @input="getMediaLibrary"
                                                ></v-pagination>
                                            </v-expansion-panel-content>
                                        </v-expansion-panel>
                                    </v-expansion-panels>
                                </v-expansion-panel-content>
                            </v-expansion-panel>
                        </v-expansion-panels>
                        <v-card-actions class="pt-3 pb-0">
                            <v-btn
                                color="error"
                                to="/terminals/slideshows"
                                @click="dialog = false"
                            >
                                Cancel
                            </v-btn>
                            <v-spacer />
                            <v-btn color="success" @click="updateSlideshow()">
                                Update
                            </v-btn>
                        </v-card-actions>
                    </v-card-text>
                </v-card>
            </v-container>
        </v-dialog>
        <v-row class="justify-center">
            <v-col cols="12" md="7">
                <v-data-table
                    :headers="headers"
                    :items="slideshows"
                    hide-default-footer
                    disable-pagination
                    :loading="loading"
                    loading-text="Loading... Please wait"
                    class="elevation-3"
                >
                    <template v-slot:top>
                        <v-toolbar color="green" class="rounded-t-lg" dark flat>
                            <v-toolbar-title> Slideshows </v-toolbar-title>
                            <v-spacer />
                            <v-btn depressed light to="/add-slideshow">
                                New Slideshow
                            </v-btn>
                        </v-toolbar>
                    </template>
                    <!-- <template v-slot:[`slideshows.thumbnail`]="{ item }">
                        <div class="py-2">
                            <v-img
                                :src="item.thumbnail"
                                :alt="item.id.name"
                                width="100"
                                height="70"
                                class="rounded"
                            />
                        </div>
                    </template> -->
                    <template v-slot:[`item.actions`]="{ item }">
                        <v-menu transition="scroll-y-transition" bottom left>
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn icon v-bind="attrs" v-on="on">
                                    <v-icon> mdi-dots-vertical </v-icon>
                                </v-btn>
                            </template>
                            <v-list>
                                <v-list-item>
                                    <template>
                                        <v-btn
                                            color="warning"
                                            text
                                            v-on="on"
                                            @click="openEditDialog(item)"
                                        >
                                            <v-icon small left>
                                                mdi-pencil
                                            </v-icon>
                                            Edit
                                        </v-btn>
                                    </template>
                                </v-list-item>
                                <v-list-item>
                                    <v-btn
                                        color="error"
                                        text
                                        @click="deleteSlideshow(item)"
                                    >
                                        <v-icon small left> mdi-delete </v-icon>
                                        Delete
                                    </v-btn>
                                </v-list-item>
                            </v-list>
                        </v-menu>
                    </template>
                    <template v-slot:no-data>
                        <v-alert prominent text type="info" class="ma-2">
                            <v-row align="center">
                                <v-col class="grow"> No records found </v-col>
                                <v-col class="shrink">
                                    <v-btn
                                        color="green"
                                        class="my-2 white--text"
                                        @click="initialize"
                                    >
                                        Retry
                                    </v-btn>
                                </v-col>
                            </v-row>
                        </v-alert>
                    </template>
                </v-data-table>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import axios from 'axios';
import draggable from 'vuedraggable';
import { mapGetters } from 'vuex';
// import { updateTerminalBrandingAsset } from './BrandingScreen.vue';

export default {
    name: 'SlideshowScreen',
    components: {
        draggable,
    },
    data() {
        return {
            loading: false,
            alert: false,
            alertMessage: '',
            alertType: 'error',
            dialog: false,
            upload: false,
            images: [],
            slideshows: [],
            slideshow: [],

            pagesTotal: '',
            page: 1,
            name: '',
            delay: '',
            slides: [],
            newImage: [],
            id: null,
            headers: [
                // {
                //     text: 'Asset',
                //     align: 'left',
                //     value: 'thumbnail',
                //     sortable: false,
                //     filterable: false,
                // },
                {
                    text: 'Name',
                    align: 'left',
                    value: 'name',
                    sortable: false,
                    filterable: false,
                },
                {
                    text: 'Actions',
                    align: 'right',
                    value: 'actions',
                    sortable: false,
                    filterable: false,
                },
            ],
        };
    },
    computed: {
        ...mapGetters(['getInternalCoreClearController']),
    },
    mounted() {
        this.initialize();
    },
    methods: {
        initialize() {
            this.getSlideshowList();
        },
        async openEditDialog(item) {
            this.dialog = true;
            this.getMediaLibrary();

            try {
                let response = await axios.get(
                    `${
                        this.getInternalCoreClearController + '/get_slide_show'
                    }`,
                    {
                        params: { slide_show_id: item.id },
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response) {
                    console.log(response.data);
                    this.slideshow = response.data;
                }
            } catch (error) {
                console.error('Could not get slideshow list');
                console.error(error);
            }
        },

        async getSlideshowList() {
            try {
                let response = await axios.get(
                    `${this.getInternalCoreClearController + '/slide_shows'}`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data) {
                    // console.log('Message :)');
                    // console.log(response);
                    this.slideshows = response.data.results;
                    // console.log(this.slideshows);
                } else {
                    this.slideshows = [];
                    console.log('Slideshow list not found!');
                }
            } catch (error) {
                console.error('Could not get slideshow list');
                console.error(error);
            }
        },
        async deleteSlideshow(item) {
            // console.log(item);
            // let formData = new FormData();
            // formData.append('delete_slide_show', item.id);
            try {
                let response = await axios.get(
                    `${
                        this.getInternalCoreClearController +
                        '/delete_slide_show'
                    }`,
                    // formData,
                    {
                        params: { slide_show_id: item.id },
                        withCredentials: true,
                        crossDomain: true,
                        // headers: {
                        //     'Content-Type': 'multipart/form-data',
                        // },
                    },
                );
                if (response.data.success === true) {
                    // console.log('deleted');

                    this.alert = true;
                    this.alertType = 'success';
                    this.alertMessage = 'Slideshow deleted';
                    setTimeout(() => {
                        location.reload();
                    }, 2000);
                } else {
                    console.log(response);
                    console.log('Slideshow not found!');
                }
            } catch (error) {
                console.error('Could not get slideshow');
                console.error(error);
            }
        },
        async updateSlideshow() {
            if (this.name == '') {
                this.name = this.slideshow.name;
            }
            if (this.delay == '') {
                this.delay = this.slideshow.delay;
            }

            let formData = new FormData();
            formData.append('name', this.name);
            formData.append('delay', this.delay);
            formData.append('slide_show_id', this.slideshow.id);

            this.slideshow.slides.forEach((slide) => {
                formData.append('slides[]', JSON.stringify(slide));
            });
            try {
                let response = await axios.post(
                    `${
                        this.getInternalCoreClearController +
                        '/update_slide_show'
                    }`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data) {
                    console.log(response);
                    this.alert = true;
                    this.alertType = 'success';
                    this.alertMessage = 'Asset updated';
                    this.updateTerminalBrandingAsset(this.slideshow.id);
                    setTimeout(() => {
                        location.reload();
                    }, 2000);
                } else {
                    console.log('Slideshow not found!');
                }
            } catch (error) {
                console.error('Could not get slideshow');
                console.error(error);
            }
        },
        async uploadMedia() {
            let formData = new FormData();
            formData.append(
                'upload_media',
                this.originalFile,
                this.originalFile.name,
            );

            try {
                let response = await axios.post(
                    `${this.getInternalCoreClearController + '/upload_media'}`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        headers: {
                            'Content-Type': 'multipart/form-data',
                        },
                    },
                );

                if (response.data) {
                    console.log(response.data);
                    console.log(response.data.id);
                    console.log(this.slideshow.slides);
                    this.getMediaAsset(response.data.id);
                    console.log(this.newImage);
                    // this.slideshow.slides.push()
                } else {
                    console.log('Unable to upload');
                }
            } catch (error) {
                console.error('Could not upload media');
                console.error(error);
            }
        },
        async getMediaLibrary(input) {
            try {
                let response = await axios.get(
                    `${this.getInternalCoreClearController + '/get_media'}`,
                    {
                        params: { page: input },
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data) {
                    this.pagesTotal = response.data.pages;
                    console.log(response.data);
                    this.images = response.data.results;
                } else {
                    this.images = [];
                    console.log('Media library not found!');
                }
            } catch (error) {
                console.error('Could not get media library');
                console.error(error);
            }
        },
        async getMediaAsset(id) {
            try {
                let response = await axios.get(
                    `${
                        this.getInternalCoreClearController + '/get_media_asset'
                    }`,
                    {
                        params: { media_id: id },
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data) {
                    console.log(response.data);
                    this.newImage = {
                        description: response.data.description || '',
                        fileUrl: response.data.fileUrl,
                        id: response.data.id,
                        name: response.data.name || '',
                        originalFile: response.data.originalFile,
                        tags: response.data.tags || null,
                        thumbnailUrl: response.data.thumbnailUrl,
                        video: response.data.video || false,
                    };

                    this.slideshow.slides.push(this.newImage);
                } else {
                    console.log('Media asset not found!');
                }
            } catch (error) {
                console.error('Could not get media library');
                console.error(error);
            }
        },
        removeSlide(slide) {
            const mediaIdToRemove = slide.mediaId;

            const indexToRemove = this.slideshow.slides.findIndex(
                (slide) => slide.mediaId === mediaIdToRemove,
            );

            if (indexToRemove !== -1) {
                this.slideshow.slides.splice(indexToRemove, 1);
            }

            console.log(this.slideshow.slides);
        },
        async updateTerminalBrandingAsset(id) {
            let formData = new FormData();
            // formData.append('asset_id', this.editBrandingAsset.assetId);

            formData.append('slide_show_id', id);

            try {
                let response = await axios.post(
                    `${
                        this.getInternalCoreClearController +
                        '/update_branding_asset'
                    }`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        headers: {
                            'Content-Type': 'multipart/form-data',
                        },
                    },
                );
                console.log(response);
                if (response.data.success === true) {
                    console.log(response.data);
                    // this.alert = true;
                    // this.alertType = 'success';
                    // this.alertMessage = 'Asset updated';
                    // setTimeout(() => {
                    //     location.reload();
                    // }, 2000);
                }
            } catch (error) {
                console.error('Could not update');
                console.error(error);
            }
        },
    },
};
</script>
